
:root {
  --background:#FFFFFF;
  --white: #FFFFFF;
  
  --section-1: #E7EDF7;
  --section-2: #C9D4EB;

  --surface: #fffffd;
  --surface-1: #F9FAFC;
  --surface-2: #F3F7FF;

  --bg-highlight-1: #2563EB;
  --bg-highlight-2: #4682B4;
  --bg-highlight-3: #1a365d;
  --bg-accent: #0F4C5C;
  --bg-gray: #CBCDCF;





  --text-header-1: #1B2A6A;
  --text-header-2: #506D84;
  --text-header-3: #2C3E50;

  --text-highlight-1: #2563EB;
  --text-highlight-2: #4682B4;
  --text-highlight-3: #1a365d;


  --text-gray: #CBCDCF;
  --text-normal: #2C3E50;
  --text-muted: #6B7280;
  --text-strong: #374151;
  --text-soft: #4B5563;
  --text-accent: #0F4C5C;




  --shadow-xs: 0 0 3px 1px rgba(0, 0, 0, 0.06);
  --shadow-sm: 0 0 6px 2px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 0 10px 3px rgba(0, 0, 0, 0.12);
  --shadow-lg: 0 0 18px 5px rgba(0, 0, 0, 0.15);
  --shadow-xl: 0 0 28px 8px rgba(0, 0, 0, 0.2);

  --gradient-hero: linear-gradient(135deg, rgba(26, 54, 93, 0.95) 0%, rgba(44, 90, 160, 0.9) 100%);
  --gradient-primary: linear-gradient(135deg, #1a365d 0%, #020202 100%);
  --gradient-secondary: linear-gradient(135deg, #4a90e2 0%, #319795 100%);


}

body{
  background: var(--background);
}


$color-map: (
  transparent: transparent,

  header-1: var(--text-header-1),
  header-2: var(--text-header-2),
  header-3: var(--text-header-3),

  highlight-1: var(--text-highlight-1),
  highlight-2: var(--text-highlight-2),
  highlight-3: var(--text-highlight-3),

  white: var(--white),
  gray: var(--text-gray),
  normal: var(--text-normal),
  muted : var(--text-muted ),
  strong: var(--text-strong),
  soft: var(--text-soft),
  accent: var(--text-accent),

);

$bg-map: (
  transparent: transparent,

  section-1: var(--section-1),
  section-2: var(--section-2),
  surface-1: var(--surface-1),
  surface-2: var(--surface-2),

  highlight-1: var(--bg-highlight-1),
  highlight-2: var(--bg-highlight-2),
  highlight-3: var(--bg-highlight-3),

  white: var(--white),
  accent: var(--bg-accent),
  gray: var(--text-gray),

);

$sides: top, right, bottom, left;

$shadows: xs,sm,md,lg,xl;



@each $key, $color in $color-map {
  // Base text color utilities with hover, focus, and disabled states
  .color-#{$key} { color: #{$color}; }
  .hover\:color-#{$key}:hover { color: #{$color}; }
  .focus\:color-#{$key}:focus { color: #{$color}; }
  .disabled\:color-#{$key}:disabled { color: #{$color}; }

  // Border color utilities with support for hover, focus
  .bc-#{$key} { border-color: #{$color}; }
  .hover\:bc-#{$key}:hover { border-color: #{$color}; }
  .focus\:bc-#{$key}:focus { border-color: #{$color}; }

  // svg fill, stroke utilities with support for hover
  .fill-#{$key} { svg {fill : #{$color};} }
  .hover\:fill-#{$key}:hover { svg {fill : #{$color};} }
  .stroke-#{$key} { svg {stroke : #{$color};} }
  .hover\:stroke-#{$key}:hover { svg {stroke : #{$color};} }
}

// Background color utilities with hover, focus
@each $key, $color in $bg-map {
  .bg-#{$key} { background-color: #{$color}; }
  .hover\:bg-#{$key}:hover { background-color: #{$color}; }
  .focus\:bg-#{$key}:focus { background-color: #{$color}; }
}

//  Shadow Utilities
@each $key in $shadows {
  .shadow-#{$key} { box-shadow: var(--shadow-#{$key}); }
  .hover\:shadow-#{$key}:hover { box-shadow: var(--shadow-#{$key}); }
  .focus\:shadow-#{$key}:focus { box-shadow: var(--shadow-#{$key}); }
  .disabled\:shadow-#{$key}:disabled { box-shadow: var(--shadow-#{$key}); }
}

//Notch Utilities - All Sides
// Always visible notch
@each $side in $sides {
  .notch-#{$side}{
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      
      left: if($side == right, auto, 0);
      right: if($side == left, auto, 0);
      top: if($side == bottom, auto, 0);
      bottom: if($side == top, auto, 0);

      width: if($side == top or $side == bottom, 100%, var(--thickness,4px));
      height: if($side == left or $side == right, 100%, var(--thickness,4px));

      background: var(--bg, var(--gradient-secondary));
    }
  }
}

// Hover-only notch
@each $side in $sides {
  .notch-#{$side}-hover{
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;

      left: if($side == right, auto, 0);
      right: if($side == left, auto, 0);
      top: if($side == bottom, auto, 0);
      bottom: if($side == top, auto, 0);

      width: if($side == top or $side == bottom, 100%, var(--thickness,4px));
      height: if($side == left or $side == right, 100%, var(--thickness,4px));

      background: var(--bg, var(--gradient-secondary));
      opacity: 0;
      transition: opacity var(--transition, var(--transition-fast));
    }
  }

  .notch-#{$side}-hover:hover::before {
    opacity: 1;
  }
}


// -----------------------------
// ðŸ“± Responsive Utility Variants
// -----------------------------

@each $break, $size in $breakpoints {
  @media (min-width: #{$size}) {

    @each $key, $color in $color-map {
      // Base text color utilities with hover, focus, and disabled states
      .#{$break}\:color-#{$key} { color: #{$color}; }
      .#{$break}\:hover\:color-#{$key}:hover { color: #{$color}; }
      .#{$break}\:focus\:color-#{$key}:focus { color: #{$color}; }
      .#{$break}\:disabled\:color-#{$key}:disabled { color: #{$color}; }

      // Border color utilities with support for hover, focus
      .#{$break}\:bc-#{$key} { border-color: #{$color}; }
      .#{$break}\:hover\:bc-#{$key}:hover { border-color: #{$color}; }
      .#{$break}\:focus\:bc-#{$key}:focus { border-color: #{$color}; }

      // svg fill, stroke utilities with support for hover
      .#{$break}\:fill-#{$key} { svg {fill : #{$color};} }
      .#{$break}\:hover\:fill-#{$key}:hover { svg {fill : #{$color};} }
      .#{$break}\:stroke-#{$key} { svg {stroke : #{$color};} }
      .#{$break}\:hover\:stroke-#{$key}:hover { svg {stroke : #{$color};} }
    }

    // Background color utilities with hover, focus
    @each $key, $color in $bg-map {
      .#{$break}\:bg-#{$key} { background-color: #{$color}; }
      .#{$break}\:hover\:bg-#{$key}:hover { background-color: #{$color}; }
      .#{$break}\:focus\:bg-#{$key}:focus { background-color: #{$color}; }
    }

    //  Shadow Utilities
    @each $key in $shadows {
      .#{$break}\:shadow-#{$key} { box-shadow: var(--shadow-#{$key}); }
      .#{$break}\:hover\:shadow-#{$key}:hover { box-shadow: var(--shadow-#{$key}); }
      .#{$break}\:focus\:shadow-#{$key}:focus { box-shadow: var(--shadow-#{$key}); }
      .#{$break}\:disabled\:shadow-#{$key}:disabled { box-shadow: var(--shadow-#{$key}); }
    }
  }
}

