// =============================================================================
// ========== START: Base Gradient Text Mixin ==================================
// =============================================================================
//
// Mixin to apply a gradient text effect using customizable settings.
//
// Settings Map:
//   - c1: Primary gradient start color (default: #ffffa2)
//   - base: Optional fallback text color. If provided, it overrides c1 for text color.
//
// This mixin:
//   - Sets up gradient clipping to text using background-clip
//   - Uses WebKit-specific properties for compatibility (e.g., Safari, Chrome)
//   - Makes text transparent to reveal gradient underneath
//
// =============================================================================

@mixin base-gradient-text($settings: ()) {
  $c1: map-get($settings, c1);
  $base: map-get($settings, base);

  color: if($base != null, $base, $c1);

  -webkit-text-fill-color: transparent !important;
  -webkit-background-clip: text !important;
  background-clip: text !important;
}

// =============================================================================
// ========== END: Base Gradient Text Mixin ====================================
// =============================================================================

// =============================================================================
// ========== START: Gradient Rotation Mixins ==================================
// =============================================================================
// These mixins apply animated, rotating gradient effects to both backgrounds
// and text. They feature graceful fallback mechanisms, customizable settings
// via a map-based approach, and ensure cross-browser compatibility.
// =============================================================================


// Mixin: gradient-bg-rotate
// Description: This mixin applies a customizable rotating gradient background
// to an element. You can pass various settings, including the gradient colors,
// direction, base color (for fallback), and an option to trigger rotation animation.
//
// Settings Map (optional keys):
//   - c1 (default: #ffffa2): Start color of the gradient
//   - c2 (default: #adf8fc): End color of the gradient
//   - deg (default: 135deg): The direction of the gradient (in degrees)
//   - base (default: null): Base background color, used if no gradient is desired.
//     If provided, it will override the gradient background.
//   - animate (default: true): Boolean flag to trigger gradient rotation animation.
//   - duration (default: 6s): Duration for the rotation animation.
//   - timing (default: linear): Specifies the timing function for the animation cycle, controlling the speed curve.
//   - iteration (default: infinite): Sets the number of animation repetitions; "infinite" for endless loops.
//   - direction (default: normal): Direction of animation. Options include:
//     - normal: Standard direction
//     - reverse: Reverse direction
//     - alternate: Alternate direction for each cycle
//     - alternate-reverse: Reverse alternate direction
//
@mixin gradient-bg-rotate($settings: ()) {
  // Default settings for the mixin
  $defaults: (
    c1: #ffffa2,
    c2: #adf8fc,
    deg: 135deg,
    base: null,
    animate: true,
    duration: 6s,
    timing: linear,
    iteration: infinite,
    direction: normal,
  );

  // Valid options for direction setting
  $valid-dir: normal, reverse, alternate, alternate-reverse;

  // Merge user-provided settings with defaults
  $final: map-merge($defaults, $settings);

  // Extract the final values for each property
  $c1: map-get($final, c1);
  $c2: map-get($final, c2);
  $deg: map-get($final, deg);
  $base: map-get($final, base);
  $animate: map-get($final, animate);
  $duration: map-get($final, duration);
  $timing: map-get($final, timing);
  $iteration: map-get($final, iteration);
  $direction: if(index($valid-dir, map-get($final, direction)), map-get($final, direction), normal);

  // Apply base color if provided, otherwise fall back to $c1 for background
  background-color: if($base != null, $base, $c1);

  // Apply linear gradient in multiple vendor-specific formats
  @include gradientBackground(linear-gradient($deg, $c1, $c2));

  // If animation is enabled, set up the rotating gradient effect
  @if $animate == true {
    @supports (--angle: 0deg) {
      // Apply the CSS custom properties for the angle-based gradient rotation
      @include gradientBackground(linear-gradient(var(--angle), $c1, $c2));

      // Generate unique animation name using deg
      $anim-name: rotate-angle-#{$deg};

      // Define keyframes for rotating the gradient angle from its starting to ending angle
      @include keyframes(#{$anim-name}){
        from { --angle: #{$deg}; }
        to { --angle: #{$deg + 360deg}; }
      }

      // Include animation with infinite looping to rotate the gradient
      @include animation(#{$anim-name} #{$duration} #{$timing} #{$iteration} #{$direction});
    }
  }
}


// Mixin: gradient-text-rotate
// Description: This mixin applies a rotating gradient text effect with optional animation.
// It ensures the text fill is transparent and the gradient is clipped to the text itself.
// Uses the gradient-bg-rotate mixin to provide rotating gradient functionality.
//
// Note: This mixin relies on the `base-gradient-text` mixin for applying the base
// gradient text effect. Ensure that `base-gradient-text` is defined elsewhere.
@mixin gradient-text-rotate($settings: ()) {
  // Apply the base gradient text effect from the base-gradient-text mixin
  @include base-gradient-text($settings);

  // Include the rotating gradient background mixin for the text
  @include gradient-bg-rotate($settings);

  // Prevent text selection to ensure the effect is applied smoothly
  @include user-select-none;
}

// =============================================================================
// ========== END: Gradient Rotation Mixins ====================================
// =============================================================================


// =============================================================================
// ========== START: Striped Gradient Mixins ====================
// =============================================================================

// Mixin: gradient-bg-striped
// Description: This mixin creates a striped gradient background with an optional
// animation that moves the stripes horizontally. It is customizable with options
// for colors, stripe size, gradient angle, and animation speed. This is perfect
// for backgrounds with dynamic moving patterns.
//
// Settings Map:
//   - c1 (default: #ffffa2): Start color of the gradient stripes
//   - c2 (default: #adf8fc): End color of the gradient stripes
//   - s (default: 50px): Size of each stripe
//   - deg (default: 135deg): Angle of the gradient stripes (in degrees)
//   - base (default: null): Base background color for fallback. If provided,
//     this will be used in place of the gradient.
//   - animate (default: true): Flag to enable/disable the animation.
//     When true, the stripes move horizontally across the background.
//   - duration (default: 2s): Duration of the animation cycle
//   - timing (default: linear): Specifies the timing function for the animation cycle, controlling the speed curve.
//   - iteration (default: infinite): Sets the number of animation repetitions; "infinite" for endless loops.
//   - direction (default: normal): Direction of the animation. Options include:
//     - normal: Standard movement direction
//     - reverse: Reversed direction
//     - alternate: Alternating direction for each cycle
//     - alternate-reverse: Reverse alternate direction
//
@mixin gradient-bg-striped($settings: ()) {
  // Default settings for the mixin
  $defaults: (
    c1: #ffffa2,
    c2: #adf8fc,
    s: 50px,
    deg: 135deg,
    animate: true,
    duration: 2s,
    timing: linear,
    iteration: infinite,
    direction: normal,
  );

  // Valid options for direction setting
  $valid-dir: normal, reverse, alternate, alternate-reverse;

  // Merge user-provided settings with defaults
  $final: map-merge($defaults, $settings);

  // Extract final values from the settings map
  $c1: map-get($final, c1);
  $c2: map-get($final, c2);
  $s: map-get($final, s);
  $deg: map-get($final, deg);
  $animate: map-get($final, animate);
  $duration: map-get($final, duration);
  $timing: map-get($final, timing);
  $iteration: map-get($final, iteration);
  $direction: if(index($valid-dir, map-get($final, direction)), map-get($final, direction), normal);

  // Apply base background color if provided, otherwise fallback to $c1
  background-color: $c1;

  // Apply the striped gradient background pattern using vendor prefixes
  @include gradientBackground(linear-gradient($deg, $c2 25%, transparent 25%, transparent 50%, $c2 50%, $c2 75%, transparent 75%, transparent));

  // Set the size of the stripes
  background-size: $s $s;

  // If animation is enabled, create a flowing effect for the stripes
  @if $animate == true {
    // Generate unique animation name based on the stripe size
    $anim-name: pbs-#{$s};

    // Define keyframes for animating the background position to create stripe movement
    @include keyframes(#{$anim-name}){
      from { background-position: 0 0;}
      to { background-position: $s 0; }
    }

    // Apply the animation for continuous stripe movement in the specified direction
    @include animation(#{$anim-name} #{$duration} #{$timing} #{$iteration} #{$direction});
  }
}

// =============================================================================
// ========== END: Striped Gradient Mixins ======================
// =============================================================================

// =============================================================================
// ========== START: Gradient flow Mixin =========================
// =============================================================================

// Mixin to create a background with a flowing gradient effect, with customizable colors, direction,
// optional animation, and a smooth flow effect using a custom property.
// It provides the functionality to stretch the gradient background dynamically by using the custom
// CSS property `--distance` and supports browser prefixes for cross-browser compatibility.
//
// Settings Map:
//   - c1 (default: #ffffa2): Start color of the gradient
//   - c2 (default: #adf8fc): End color of the gradient
//   - deg (default: 135deg): Direction of the gradient in degrees
//   - base (default: null): Base color for fallback
//   - animate (default: true): Boolean flag to trigger animation (flowing effect)
//   - duration (default: 3s): Duration for the flowing animation
//   - timing (default: linear): Specifies the timing function for the animation cycle, controlling the speed curve.
//   - iteration (default: infinite): Sets the number of animation repetitions; "infinite" for endless loops.
//   - direction (default: alternate): Direction of the animation.
//
@mixin gradient-bg-flow($settings: ()) {
  // Default settings with fallback values
  $defaults: (
    c1: #ffffa2,
    c2: #adf8fc,
    deg: 135deg,
    base: null,
    animate: true,
    duration: 3s,
    timing: linear,
    iteration: infinite,
    direction: alternate,
  );

  // Valid options for direction setting
  $valid-dir: normal, reverse, alternate, alternate-reverse;

  // Merge user-provided settings with defaults
  $final: map-merge($defaults, $settings);

  // Extract the final values for each property
  $c1: map-get($final, c1);
  $c2: map-get($final, c2);
  $deg: map-get($final, deg);
  $base: map-get($final, base);
  $animate: map-get($final, animate);
  $duration: map-get($final, duration);
  $timing: map-get($final, timing);
  $iteration: map-get($final, iteration);
  $direction: if(index($valid-dir, map-get($final, direction)), map-get($final, direction), normal);

  // Apply base color if provided, otherwise fall back to $c1 for background
  background: if($base != null, $base, $c1);

  // Apply linear gradient in multiple vendor-specific formats
  @include gradientBackground(linear-gradient($deg, $c1, $c2));



  // If animation is enabled, set up the flowing gradient effect
  @if $animate == true {
    @supports (--distance: 0.5) {
      // Create a gradient that flows based on the custom property
      @include gradientBackground(linear-gradient($deg, $c1 0%, $c2 var(--distance), $c1 100%));

      // Apply an animation for a smooth flow effect
      @include animation(grow-distance #{$duration} #{$timing} #{$iteration} #{$direction});
    }
  }
}

// Mixin to create gradient text effects, with optional flowing background
// and animation. This ensures a smooth and dynamic flowing gradient text effect with rotating gradient
// background functionality. Prevents text selection to enhance visual experience.
//
// Note: This mixin relies on the `base-gradient-text` mixin for applying the base
// gradient text effect. Ensure that `gradient-bg-flow` is defined elsewhere.

@mixin gradient-text-flow($settings: ()) {
  // Apply the base gradient text effect from the base-gradient-text mixin
  @include base-gradient-text($settings);

  // Include the flowing gradient background mixin for the text
  @include gradient-bg-flow($settings);

  // Prevent text selection to ensure the effect is applied smoothly
  @include user-select-none;
}

// =============================================================================
// ========== END: Gradient Stretch Mixin =================================
// =============================================================================



// =============================================================================
// ========== START: Gradient Traverse Mixin ==================================
// =============================================================================
//
// Mixin to create a background with a traversing (striped-motion) gradient effect.
// It allows fully customizable colors, gradient direction, and animation control.
// The animation simulates a continuous movement of color stripes across the background
// using the `--distance` CSS custom property.
//
// Settings Map:
//   - c1 (default: #ffffa2): Primary color used in the gradient
//   - c2 (default: #adf8fc): Secondary color creating the stripe
//   - deg (default: 135deg): Direction/angle of the gradient
//   - base (default: null): Optional fallback base color
//   - animate (default: true): Enables/disables animation
//   - duration (default: 2s): Duration of the animation cycle
//   - timing (default: linear): Specifies the timing function for the animation cycle, controlling the speed curve.
//   - iteration (default: infinite): Sets the number of animation repetitions; "infinite" for endless loops.
//   - direction (default: alternate): Direction of the animation.
//   - sw (default: 4%): Width of each moving stripe
//   - ss (default: 10%): Space between the stripes
//
@mixin gradient-bg-traverse($settings: ()) {
  // Default settings with fallback values
  $defaults: (
    c1: #ffffa2,
    c2: #adf8fc,
    deg: 135deg,
    base: null,
    animate: true,
    duration: 2s,
    timing: linear,
    iteration: infinite,
    sw: 4%,
    ss: 10%,
    direction: normal,
  );

  // Valid options for direction setting
  $valid-dir: normal, reverse, alternate, alternate-reverse;

  // Merge user-provided settings with defaults
  $final: map-merge($defaults, $settings);

  // Extract the final values for each property
  $c1: map-get($final, c1);
  $c2: map-get($final, c2);
  $deg: map-get($final, deg);
  $base: map-get($final, base);
  $animate: map-get($final, animate);
  $duration: map-get($final, duration);
  $timing: map-get($final, timing);
  $iteration: map-get($final, iteration);
  $sw: map-get($final, sw);
  $ss: map-get($final, ss);
  $direction: if(index($valid-dir, map-get($final, direction)), map-get($final, direction), normal);

  // Apply base color if provided, otherwise fall back to $c1 for background
  background: if($base != null, $base, $c1);

  // Apply linear gradient in multiple vendor-specific formats
  @include gradientBackground(linear-gradient($deg, $c1 50%, $c2 calc(50% + #{$ss}), $c2 calc(50% + #{$ss} + #{$sw}), $c1 calc(50% + #{$ss * 2} + #{$sw})));

  // If animation is enabled, set up the traversing gradient effect
  @if $animate == true {
    @supports (--distance: 50%) {

      @include gradientBackground(linear-gradient(
        $deg,
        $c1 calc(var(--distance)),
        $c2 calc(var(--distance) + #{$ss}),
        $c2 calc(var(--distance) + #{$ss} + #{$sw}),
        $c1 calc(var(--distance) + #{$ss * 2} + #{$sw})
      ));


      // Generate unique animation name using sw + ss
      $anim-name: traverse-bg-#{str-slice(unquote("#{$sw}"), 1, -2)}-#{str-slice(unquote("#{$ss}"), 1, -2)};

      //  Define keyframes for gradient animation
      @include keyframes(#{$anim-name}){
        from { --distance: #{-($ss * 2 + $sw)}; }
        to   { --distance: 100%; }
      }

      // Apply an animation for a smooth travers effect
      @include animation(#{$anim-name} #{$duration} #{$timing} #{$iteration} #{$direction});
    }
  }
}

// Mixin to apply the traversing animated gradient effect to text elements.
// Combines a background traversal animation with gradient text styling for a
// visually appealing animated text look.
//
// Note: This mixin relies on the `base-gradient-text` mixin for applying the base
// gradient text effect. Ensure that `gradient-bg-traverse` is defined elsewhere.

@mixin gradient-text-traverse($settings: ()) {
  // Apply the base gradient text effect from the base-gradient-text mixin
  @include base-gradient-text($settings);

  // Include the traversing gradient background mixin for the text
  @include gradient-bg-traverse($settings);

  // Prevent text selection to ensure the effect is applied smoothly
  @include user-select-none;
}

// =============================================================================
// ========== END: Gradient Traverse Mixin ====================================
// =============================================================================
