// Define animatable custom properties using @property (CSS Houdini)
// These enable smooth transitions for CSS variables with defined syntax.

@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

@property --stretch {
  syntax: '<number>';
  initial-value: 0;
  inherits: false;
}

@property --distance {
  syntax: '<percentage>';
  initial-value: 0.0001%;
  inherits: false;
}
@property --distance2 {
  syntax: '<percentage>';
  initial-value: 0.0001%;
  inherits: false;
}



// Keyframe animation for --distance
@include keyframes(grow-distance){
  from { --distance: 0%; }
  to   { --distance: 100%; }
}
