
$breakpoints: (
  xxs: 360px,
  xs: 480px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);




// Font-size and line-height utilities from $font-sizes
@each $key, $size in $font-sizes {
  .fs-#{$key} { font-size: #{$size}; }
  .lh-#{$key} { line-height: #{$size}; }
}

// Line-height utilities from named values in $line-heights
@each $key, $value in $line-heights {
  .lh-#{$key} { line-height: #{$value}; }
}

// Font weight utilities for controlling text boldness
@each $key, $weight in $font-weights {
  .fw-#{$key} { font-weight: #{$weight}; }
}

// Letter-spacing utilities from $letter-spacing map
@each $key, $value in $letter-spacing {
  .ls-#{$key} { letter-spacing: #{$value}; }
}

// Text alignment utilities for left, center, right, and justified text
@each $align in $text-alignments {
  .text-#{$align} { text-align: #{$align}; }
}

// Border radius utilities for rounding element corners
@each $key, $radius in $radius-map {
  .br-#{$key} { border-radius: #{$radius}; }
}

// Border width utilities to control thickness of borders
@each $key, $width in $border-widths {
  .bw-#{$key} { border-width: #{$width}; }
  .bw-t-#{$key} { border-top-width: #{$width}; }
  .bw-r-#{$key} { border-right-width: #{$width}; }
  .bw-b-#{$key} { border-bottom-width: #{$width}; }
  .bw-l-#{$key} { border-left-width: #{$width}; }
  .bw-x-#{$key} {
    border-left-width: #{$width};
    border-right-width: #{$width};
  }
  .bw-y-#{$key} {
    border-top-width: #{$width};
    border-bottom-width: #{$width};
  }
}

// Border style utilities for solid, dashed, dotted, double, and none
@each $style in $border-styles {
  .bs-#{$style} { border-style: #{$style}; }
}

// Margin and padding utilities
@each $key, $space in $space-map {
    /* All sides */
    .p-#{$key} { padding: #{$space}; }
    .m-#{$key} { margin: #{$space}; }

    /* Only top */
    .pt-#{$key} { padding-top: #{$space}; }
    .mt-#{$key} { margin-top: #{$space}; }

    /* Only right */
    .pr-#{$key} { padding-right: #{$space}; }
    .mr-#{$key} { margin-right: #{$space}; }

    /* Only bottom */
    .pb-#{$key} { padding-bottom: #{$space}; }
    .mb-#{$key} { margin-bottom: #{$space}; }

    /* Only left */
    .pl-#{$key} { padding-left: #{$space};  }
    .ml-#{$key} { margin-left: #{$space};  }

    /* Only  Horizontal (left + right) */
    .px-#{$key} { padding-left: #{$space}; padding-right: #{$space}; } 
    .mx-#{$key} { margin-left: #{$space}; margin-right: #{$space}; } 

    /* Only top Vertical (top + bottom)*/
    .py-#{$key} { padding-top: #{$space}; padding-bottom: #{$space}; }
    .my-#{$key} { margin-top: #{$space}; margin-bottom: #{$space}; }


    .top-#{$key} { top: #{$space}; }
    .right-#{$key} { right: #{$space}; }
    .bottom-#{$key} { bottom: #{$space}; }
    .left-#{$key} { left: #{$space}; }
    .inset-#{$key} { inset: #{$space}; }

}

// Width and height utility classes
@each $key, $width in $width-map {
  .w-#{$key} { width: #{$width}; }
  .h-#{$key} { height: #{$width}; }
}

@each $key, $width in $max-widths {
  .max-w-#{$key} { max-width: #{$width}; }
}

// Loop for creating grid-based columns: grid-cols-1 to grid-cols-12
@for $i from 1 through 12 {
  // Apply grid layout with $i equal-width columns
  .grid-cols-#{$i} {
    display: grid;  // Enable CSS Grid layout
    grid-template-columns: repeat(#{$i}, minmax(0, 1fr)); // Create $i columns of equal width
  }
}

// Display + width utilities, including flex-direction (row/column)
@each $display in $displays {

  .#{$display} { position:relative; display: #{$display}; }

  @if $display == flex {
    @each $dir in $flex-directions {
      .#{$dir} { flex-direction: #{$dir}; }

      @each $key, $width in $width-map {
        .#{$display}-#{$dir}-#{$key} {
          position:relative;
          display: flex;
          flex-direction: #{$dir};
          width: #{$width};
        }
      }
    }
  }@else{
    @each $key, $width in $width-map {
      .#{$display}-#{$key} {
        position:relative;
        display: #{$display};
        width: #{$width};
      }
    }
  }
}

@each $value in $positions {
  .#{$value} { position: #{$value}; }
}

// Justify-content base utilities
@each $value in $justify-options {
  .justify-#{$value} { justify-content: #{$value}; }
}

// Align-items base utilities
@each $value in $align-options {
  .items-#{$value} { align-items: #{$value}; }
}

// Gap utilities for flex/grid layouts with row, column,
@each $key, $space in $space-map {
  .gap-#{$key} { gap: $space; }
  .row-gap-#{$key} { row-gap: $space; }
  .col-gap-#{$key} { column-gap: $space; }
}

// Base shrink classes
@each $val in $shrink-values {
  .shrink-#{$val} { flex-shrink: #{$val}; }
}

@each $val in $flex-wraps {
  .wrap-#{$val} { flex-wrap: #{$val}; }
}

// Opacity utility classes
@each $key, $value in $opacity {
  .opacity-#{$key} { opacity: #{$value}; }
}

// Z-index utility classes
@each $key, $value in $z-index {
  .z-index-#{$key} { z-index: #{$value}; }
}

// Cursor utility classes
@each $key, $value in $cursor {
  .cursor-#{$key} { cursor: #{$value}; }
}

// Overflow utility classes
@each $key, $value in $overflow {
  .overflow-#{$key} { overflow: #{$value}; }
}

// Transform utility classes
@each $key, $value in $lift-map {
  .lift-#{$key}:hover {
    transform: translateY(calc(-1 * $value));
  }
  .zoom-#{$key}:hover {
    transform: scale(#{1 + ($key * 0.01)});
  }
  .float-#{$key}{
    transform: translateY(calc(-0.5 * $value));
    &:hover {
      transform: translateY(calc(-1 * $value));
    }
  }
}
@each $key, $value in $transitions {
  .transition-#{$key} { transition: all $value; }
}

// -----------------------------
// ðŸ“± Responsive Utility Variants
// -----------------------------

@each $break, $size in $breakpoints {
  @media (min-width: #{$size}) {

    // Font-size and line-height utilities from $font-sizes
    @each $key, $size in $font-sizes {
      .#{$break}\:fs-#{$key} { font-size: #{$size}; }
      .#{$break}\:lh-#{$key} { line-height: #{$size}; }
    }

    // Line-height utilities from named values in $line-heights
    @each $key, $value in $line-heights {
      .#{$break}\:lh-#{$key} { line-height: #{$value}; }
    }

    // Font weight utilities for controlling text boldness
    @each $key, $weight in $font-weights {
      .#{$break}\:fw-#{$key} { font-weight: #{$weight}; }
    }

    // Letter-spacing utilities from $letter-spacing map
    @each $key, $value in $letter-spacing {
      .#{$break}\:ls-#{$key} { letter-spacing: #{$value}; }
    }

    // Text alignment utilities for left, center, right, and justified text
    @each $align in $text-alignments {
      .#{$break}\:text-#{$align} { text-align: #{$align}; }
    }

    // Border radius utilities for rounding element corners
    @each $key, $radius in $radius-map {
      .#{$break}\:br-#{$key} { border-radius: #{$radius}; }
    }

    // Border width utilities to control thickness of borders
    @each $key, $width in $border-widths {
      .#{$break}\:bw-#{$key} { border-width: #{$width}; }
      .#{$break}\:bw-t-#{$key} { border-top-width: #{$width}; }
      .#{$break}\:bw-r-#{$key} { border-right-width: #{$width}; }
      .#{$break}\:bw-b-#{$key} { border-bottom-width: #{$width}; }
      .#{$break}\:bw-l-#{$key} { border-left-width: #{$width}; }
      .#{$break}\:bw-x-#{$key} {
        border-left-width: #{$width};
        border-right-width: #{$width};
      }
      .#{$break}\:bw-y-#{$key} {
        border-top-width: #{$width};
        border-bottom-width: #{$width};
      }
    }

    // Border style utilities for solid, dashed, dotted, double, and none
    @each $style in $border-styles {
      .#{$break}\:border-#{$style} { border-style: #{$style}; }
    }

    // Margin and padding utilities
    @each $key, $space in $space-map {
      .#{$break}\:p-#{$key} { padding: #{$space}; }
      .#{$break}\:pt-#{$key} { padding-top: #{$space}; }
      .#{$break}\:pr-#{$key} { padding-right: #{$space}; }
      .#{$break}\:pb-#{$key} { padding-bottom: #{$space}; }
      .#{$break}\:pl-#{$key} { padding-left: #{$space}; }
      .#{$break}\:px-#{$key} { padding-left: #{$space}; padding-right: #{$space}; } 
      .#{$break}\:py-#{$key} { padding-top: #{$space}; padding-bottom: #{$space}; }
      
      .#{$break}\:m-#{$key} { margin: #{$space}; }
      .#{$break}\:mt-#{$key} { margin-top: #{$space}; }
      .#{$break}\:mr-#{$key} { margin-right: #{$space}; }
      .#{$break}\:mb-#{$key} { margin-bottom: #{$space}; }
      .#{$break}\:ml-#{$key} { margin-left: #{$space}; }
      .#{$break}\:mx-#{$key} { margin-left: #{$space}; margin-right: #{$space}; } 
      .#{$break}\:my-#{$key} { margin-top: #{$space}; margin-bottom: #{$space}; } 

      .#{$break}\:top-#{$key} { top: #{$space}; }
      .#{$break}\:right-#{$key} { right: #{$space}; }
      .#{$break}\:bottom-#{$key} { bottom: #{$space}; }
      .#{$break}\:left-#{$key} { left: #{$space}; }
      .#{$break}\:inset-#{$key} { inset: #{$space}; }

    }

    // Width and height utility classes
    @each $key, $width in $width-map {
      .#{$break}\:w-#{$key} { width: #{$width}; }
      .#{$break}\:h-#{$key} { height: #{$width}; }
    }

    // Width and height utility classes for max-width breakpoints
    @each $key, $width in $max-widths {
      .#{$break}\:max-w-#{$key} { max-width: #{$width}; }
    }

    // Loop for creating responsive grid column classes at each breakpoint
    @for $i from 1 through 12 {
      // grid-cols-sm-1 to grid-cols-sm-12, etc.
      .grid-cols-#{$break}-#{$i} {
        display: grid;  // Enable CSS Grid layout
        grid-template-columns: repeat(#{$i}, minmax(0, 1fr)); // $i equal-width columns at this breakpoint
      }
    }

    // Display + width utilities, including flex-direction (row/column)
    @each $display in $displays {

      .#{$break}\:#{$display} { position:relative; display: #{$display}; }

      @if $display == flex {
        @each $dir in $flex-directions {
          .#{$break}\:#{$dir} { flex-direction: #{$dir}; }

          @each $key, $width in $width-map {
            .#{$break}\:#{$display}-#{$dir}-#{$key} {
              position:relative;
              display: flex;
              flex-direction: #{$dir};
              width: #{$width};
            }
          }
        }
      }@else{
        @each $key, $width in $width-map {
          .#{$break}\:#{$display}-#{$key} {
            position:relative;
            display: #{$display};
            width: #{$width};
          }
        }
      }
    }

    @each $value in $positions {
      .#{$break}\:#{$value} { position: #{$value}; }
    }

    // Justify-content base utilities
    @each $value in $justify-options {
      .#{$break}\:justify-#{$value} { justify-content: #{$value}; }
    }

    // Align-items base utilities
    @each $value in $align-options {
      .#{$break}\:items-#{$value} { align-items: #{$value}; }
    }

    // Gap utilities for flex/grid layouts with row, column,
    @each $key, $space in $space-map {
      .#{$break}\:gap-#{$key} { gap: $space; }
      .#{$break}\:row-gap-#{$key} { row-gap: $space; }
      .#{$break}\:col-gap-#{$key} { column-gap: $space; }
    }
    
    // Responsive shrink classes
    @each $val in $shrink-values {
      .#{$break}\:shrink-#{$val} { flex-shrink: #{$val}; }
    }

    @each $val in $flex-wraps {
      .#{$break}\:wrap-#{$val} { flex-wrap: #{$val}; }
    }

    // Responsive Opacity utility classes
    @each $key, $value in $opacity {
      .#{$break}\:opacity-#{$key} { opacity: #{$value}; }
    }

    // Responsive Z-index utility classes
    @each $key, $value in $z-index {
      .#{$break}\:z-index-#{$key} { z-index: #{$value}; }
    }

    // Responsive Cursor utility classes
    @each $key, $value in $cursor {
      .#{$break}\:cursor-#{$key} { cursor: #{$value}; }
    }

    // Responsive Overflow utility classes
    @each $key, $value in $overflow {
      .#{$break}\:overflow-#{$key} { overflow: #{$value}; }
    }

    // Responsive Transform utility classes
    @each $key, $value in $lift-map {
      .#{$break}\:lift-#{$key}:hover {
        transform: translateY(calc(-1 * $value));
      }
      .#{$break}\:float-#{$key}{
        transform: translateY(calc(-0.5 * $value));
        &:hover {
          transform: translateY(calc(-1 * $value));
        }
      }
    }
    @each $key, $value in $transitions {
      .#{$break}\:transition-#{$key} { transition: all $value; }
    }

  }
}


















