/// Auto-prefixes gradient backgrounds
@mixin gradientBackground($gradient) {
  background-image: -webkit-#{$gradient};
  background-image:    -moz-#{$gradient};
  background-image:      -o-#{$gradient};
  background-image:     -ms-#{$gradient};
  background-image:         #{$gradient};
}


// Mixin: animation
@mixin animation($animation) {
  -webkit-animation: $animation;
     -moz-animation: $animation;
       -o-animation: $animation;
          animation: $animation;
}

// Mixin: transition
@mixin transition($transitions...) {
  -webkit-transition: $transitions;
     -moz-transition: $transitions;
       -o-transition: $transitions;
          transition: $transitions;
}

/// Mixin to define cross-browser compatible keyframes
@mixin keyframes($name) {
  @-webkit-keyframes #{$name} { @content; }
     @-moz-keyframes #{$name} { @content; }
       @-o-keyframes #{$name} { @content; }
          @keyframes #{$name} { @content; }
}

/// Mixin for applying border radius
@mixin border-radius($radius: 10px) {
  -webkit-border-radius: $radius;
     -moz-border-radius: $radius;
          border-radius: $radius;
}

/// Mixin for cross-browser box-shadow
@mixin box-shadow($shadow...) {
  -webkit-box-shadow: $shadow;
     -moz-box-shadow: $shadow;
       -o-box-shadow: $shadow;
          box-shadow: $shadow;
}

/// Mixin for placeholder text styling with cross-browser support
@mixin placeholder-style() {
  &::-webkit-input-placeholder { @content; }
      &::-ms-input-placeholder { @content; }
           &::-moz-placeholder { @content; }
                &::placeholder { @content; }
}
