/* ==========================================================================
   Display Block Mixin
   --------------------------------------------------------------------------
   Creates a block-level element with customizable width, padding, and margin.
   Usage: @include display-block((width: 100%, padding: 0, margin: 0));
   ========================================================================== */
@mixin display-block($settings: ()) {
  $final: map-merge((width: 100%,padding: 0,margin: 0), $settings);
  position: relative;
  display: block;
  width: map-get($final, width);
  padding: map-get($final, padding);
  margin: map-get($final, margin);
}

/* ==========================================================================
   Flex Alignment Utilities
   --------------------------------------------------------------------------
   Utilities for aligning flex content. These mixins can be composed inside
   flex layouts to control content alignment precisely.
   ========================================================================== */

// Mixin: justify-start
// Usage: @include justify-start;
@mixin justify-start {
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
}

// Mixin: justify-end
// Usage: @include justify-end;
@mixin justify-end {
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
}

// Mixin: justify-space-between
// Usage: @include justify-space-between;
@mixin justify-space-between {
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

// Mixin: justify-space-evenly
// Usage: @include justify-space-evenly;
@mixin justify-space-evenly {
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;  // Modern browsers only
}

// Mixin: justify-center
// Usage: @include justify-center;
@mixin justify-center {
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

// Applies the appropriate justify-content mixin based on the given value.
// Supports: start, end, space-between, space-evenly. Defaults to center.
@mixin apply-justify($justify) {
  @if $justify == 'start' {
    @include justify-start;
  } @else if $justify == 'end' {
    @include justify-end;
  } @else if $justify == 'space-between' {
    @include justify-space-between;
  } @else if $justify == 'space-evenly' {
    @include justify-space-evenly;
  } @else {
    @include justify-center;
  }
}

// Mixin: align-start
// Usage: @include align-start;
@mixin align-start {
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
}

// Mixin: align-center
// Usage: @include align-center;
@mixin align-center {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

// Mixin: align-end
// Usage: @include align-end;
@mixin align-end {
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}

// Mixin: align-stretch
// Usage: @include align-stretch;
@mixin align-stretch {
  -webkit-align-items: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
}

// Applies the appropriate align-items mixin based on the given value.
// Supports: start, end, stretch. Defaults to center.
@mixin apply-align($justify) {
  @if $justify == 'start' {
    @include align-start;
  } @else if $justify == 'end' {
    @include align-end;
  } @else if $justify == 'stretch' {
    @include align-stretch;
  } @else {
    @include align-center;
  }
}

// Sets flex direction to row with full cross-browser support
@mixin flex-direction-row {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
}

// Sets flex direction to column with full cross-browser support
@mixin flex-direction-column {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

// Applies the appropriate flex-direction based on the given value.
// Supports 'row' and 'column'. Defaults to column.
@mixin apply-flex-direction($direction){
  @if $direction == 'row' {
    @include flex-direction-row;
  } @else {
    @include flex-direction-column;
  }
}


@mixin flex-row($settings: ()) {
  $merged: map-merge((direction: row,justify: center,align: center), $settings);
  @include flex-box($merged);
}
@mixin flex-column($settings: ()) {
  $merged: map-merge((direction: column,justify: start,align: start), $settings);
  @include flex-box($merged);
}

/* ==========================================================================
   Flex Box Layout Mixin
   --------------------------------------------------------------------------
   Creates a flexible box container with customizable direction, alignment,
   width, padding, and margin. Supports legacy and modern flexbox syntax.

   Parameters (optional via map):
     - width: container width (default: 100%)
     - padding: container padding (default: 0)
     - margin: container margin (default: 0)
     - direction: row | column (default: row)
     - justify: start | center | end | space-between | space-evenly (default: center)
     - align: start | center | end | stretch (default: center)

   Usage:
     @include flex-box((width: 100%, padding: 1rem, justify: space-between));
   ========================================================================== */
@mixin flex-box($settings: ()) {
  $defaults: (
    width: 100%,
    padding: 0,
    margin: 0,
    direction: row,
    justify: center,
    align: center
  );
  $final: map-merge($defaults, $settings);

  position: relative;
  display: -webkit-box;  // Older WebKit support
  display: -ms-flexbox;  // Older IE support
  display: flex;         // Modern browsers

  @include apply-flex-direction(map-get($final, direction));
  @include apply-justify(map-get($final, justify));
  @include apply-align(map-get($final, align));

  width: map-get($final, width);
  padding: map-get($final, padding);
  margin: map-get($final, margin);
}

// Mixin: user-select-none
// Prevents text selection across all modern browsers by applying necessary vendor prefixes.
// Usage: @include user-select-none;
@mixin user-select-none {
  -webkit-user-select: none;  // WebKit-based browsers (e.g., Chrome, Safari)
  -khtml-user-select: none;   // Older Konqueror browsers
  -moz-user-select: none;     // Firefox browsers
  -o-user-select: none;       // Older Opera browsers
  user-select: none;          // Standard property for modern browsers
}

// Mixin: ellipsis-text
// Applies text overflow ellipsis to prevent text from overflowing and breaking lines.
// Ensures clean truncation of text in constrained spaces with ellipsis.
// Usage: @include ellipsis-text;
@mixin ellipsis-text {
  overflow: hidden;               // Hides overflowing content
  white-space: nowrap;            // Prevents the text from wrapping
  -o-text-overflow: ellipsis;     // Older Opera browsers
  text-overflow: ellipsis;        // Modern browsers
  word-wrap: break-word;          // Allows breaking words within the container if necessary
}

// Mixin: external-margin
// Adjusts width/height by subtracting the provided horizontal and vertical margins
// Useful for elements that should be dynamically sized based on surrounding margin.
// Usage: @include external-margin((h:10px, w:15px));
@mixin external-margin($settings: ()) {
  $final: map-merge((h: 10px,w: 10px), $settings);
  $h: map-get($final, h);
  $w: map-get($final, w);
  margin: $h $w;                            // Applies horizontal and vertical margins
  width: calc(100% - (2 * $w));              // Adjusts width based on the horizontal margin
  height: calc(100% - (2 * $h));             // Adjusts height based on the vertical margin
}

// Mixin: psuedo-bg
// Adds a background around an element with adjustable width, radius, and color.
// Great for adding styled borders or shadow effects using the ::before pseudo-element.
// Usage: @include psuedo-bg((padding: 2px, radius: 8px, color: #383050));
@mixin psuedo-bg($settings: ()) {
  $final: map-merge((padding: 2px, radius: 8px, color: #383050), $settings);
  $w: map-get($final, padding);
  $r: map-get($final, radius);
  $c: map-get($final, color);

  &:before {
    position: absolute;    // Absolutely position the pseudo-element
    display: block;        // Ensure the pseudo-element is a block-level element
    content: '';           // Empty content for the pseudo-element
    background: $c;        // Background color specified by the user
    inset: $w;             // Equal distance from all four sides (top, right, bottom, left)
    border-radius: $r;     // Rounded corners with user-defined radius
    z-index: 0;            // Ensures the pseudo-element is placed behind the main element
    pointer-events: none;  // Prevent interaction issues
  }
}
