

.cardslidercontainer {
  display: block;
  position: relative;
  width: 100%;
  user-select: none;
  overflow: hidden;
  &:hover .cardslidercontainer__button{
    display: block;
  }

  .cardslidercontainer__track {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
	  justify-content: flex-start;
   	align-items: stretch;
    height: 100%;

    transform: translateX(calc(-1 * var(--current-index) * (var(--card-width) + var(--card-margin))));
    transition: transform var(--smart-speed, 500ms) ease-in-out;

    .cardslidercontainer__item {
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: start;
      flex: 0 0 auto;
      margin-right: var(--card-margin);
      overflow: hidden;
      width: var(--card-width);
    }
  }

  .cardslidercontainer__button {
    display: none;
    position: absolute;
    width: auto;
    top: calc(50% - 20px);
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    z-index: 1;
    transition: background .2s ease-in-out,fill .2s ease-in-out;
    border-radius: 5px;
    padding: 10px 8px;

    &.prev{
      left: 10px;
    }
    &.next{
      right: 10px;
      svg{transform: scale(-1,1);}
    }

    svg{
      position: relative;
      display: block;
      height: 40px;
      width: auto;
      fill:#ccc;
    }
    &:hover {
      background: rgba(0, 0, 0, 0.8);
      svg{
        fill:#fff;
      }
    }
  }

  .cardslidercontainer__dots {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
	  justify-content: center;
   	align-items: center;
    gap: 4px;
    margin-top: 24px;
    .dot{
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      height: 16px;// just to increase btn clickable height
      width: 20px;
      transition: width 0.5s ease-in-out;

      &:after{
        display: block;
        position: relative;
        width: 100%;
        transition : background-color 0.5s ease-in-out;
        content: "";
        height: 6px;
        border-radius: 5px;
        background-color: #8a8a8a;
      }
      &:hover:after{
        background-color: #2563EB;
      }
      &.active {
        width: 30px;
        &:after{
          background-color: #4682B4;
        }
      }
    }
  }

  @media (max-width: 992px){
    .cardslidercontainer__button{
      border-radius: 5px;
      padding: 8px 6px;

      &.prev{
        left: 8px;
      }
      &.next{
        right: 8px;
      }
      svg{
        height: 30px;
      }
    }
  }
  @media (max-width: 768px){
  .cardslidercontainer__dots {
    margin-top: 12px;
    gap: 6px;
    .dot{
      height: 10px;// just to increase btn clickable height
      width: 10px;
      &:after{
        height: 6px;
        border-radius: 6px;
      }
      &.active {
        width: 20px;
      }
    }
  }
  }
}
