// ============================================================================
// Global Reset and Base Styles
// ============================================================================
*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
    -webkit-tap-highlight-color: transparent;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: system-ui, 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.75;
  height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // overflow-x: hidden;
  // -ms-overflow-style: none;
  // scrollbar-width: none;
  // overflow: hidden;

  // &::-webkit-scrollbar,
  // &::-webkit-scrollbar-track,
  // &::-webkit-scrollbar-thumb,
  // &::-webkit-scrollbar-button {
  //   display: none;
  // }
}

// ============================================================================
// Typography
// ============================================================================

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
  overflow-wrap: break-word;
}

p {
  line-height: 1.75;
}

// ============================================================================
/* --------------------------------
  List Styling
-------------------------------- */

ul,
ol {
  list-style: none;
}

li {
    list-style: none;
}

// ============================================================================
/* --------------------------------
  Anchor Tag Styling
-------------------------------- */
a {
  color: inherit;
  text-decoration: none;
  outline: none;
  transition: 0.3s ease-in-out;

  &:hover,
  &:focus,
  &:active,
  &.active {
    text-decoration: none;
    outline: none;
  }
}

// ============================================================================
/* --------------------------------
  Button and Input Styling
-------------------------------- */
button {
    border: none;
    background: transparent;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    cursor: pointer;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: 0.3s ease-in-out;

    &:focus,
    &:active {
        outline: none;
    }
}

input, textarea, select {
    border: none;
  border-radius: 0;
  background: none;
  color: inherit;
  font: inherit;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: 0.3s ease-in-out;

    &:focus {
        outline: none;      // Removes outline on focus
    }


    &[type='number'] {
        -moz-appearance: textfield; // Removes spin buttons in Firefox
    }
}

select {
    &::-ms-expand {
        display: none;        // Hides dropdown arrow in IE/Edge
    }
}

// ============================================================================
/* --------------------------------
  Focus Styles
-------------------------------- */

:focus {
    outline: 0 !important;   // Removes outline for focused elements
}

:focus-visible {
    outline: 0 dotted;       // Applies a dotted outline for accessibility on focus
}
// ============================================================================


/* --------------------------------
  Media Styles
-------------------------------- */
img,
video,
canvas {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  user-select: none;
  -webkit-user-drag: none;
}

svg {
  display: block;
  width: 100%;
  height: auto;
  vertical-align: middle;
  pointer-events: none;
  user-select: none;
  transition: fill 0.3s ease-in-out, stroke 0.3s ease-in-out;
}
// ============================================================================
