@import "./mixins/mixin";

// .font-inter {font-family:'Inter', 'Segoe UI', 'Helvetica Neue', Arial, sans-serif};
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Devanagari:wght@100;200;300;400;500;600;700&display=swap');
:root {
  --font-family: 'Inter', 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
}
.font-inter{
  font-family: var(--font-family);
}


@import "./reset";
@import 'variables';
@import "./utilities";
@import "./colors";

.uppercase { text-transform: uppercase; }
.lowercase { text-transform: lowercase; }
.capitalize { text-transform: capitalize; }
.normal-case { text-transform: none; }

.text-italic { font-style: italic; }

.text-underline {text-decoration: underline;}

.resize-none {
  resize: none;
}

.select-none {
  user-select: none;
}

.scrollbar-sm{

  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  scroll-behavior: smooth;
  overscroll-behavior: contain;
  touch-action: auto;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(15, 23, 42, 0.4);
    margin: 0;
    border-radius: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #F5A623;                                                                                                           ;
    border-radius: 6px;

    &:hover {
      background-color: #be7a09;
    }
  }

  &::-webkit-scrollbar-button {
    height: 0;
    width: 0;
  }
}


